/**********************
        Menu
 **********************/


.hamb{
  height: 40px;
  margin-top:-5px;
  margin-left:100px;
}

.barrnav{
  margin-top:-55px;
  margin-right:10px;
  float:right;
}

/* En version mobile on cache les éléments du menu burger de base */

ul{
  display:none;
}

/* Toggle pour afficher le sous menu Lors du clic sur le burger */
.styleToggle{
  display:block;
  list-style: none;
}

